/* ============ Base Nav Styles ============ */
#header-nav {
  width: 100%;
  background-color: var(--nav-background-color);
  text-align: center;
  padding: 10px 0;
}

#nav-toggle {
  background: none;
  border: none;
  font-size: 2rem;
  color: var(--nav-li-color);
  cursor: pointer;
  margin-bottom: 10px;
}

#nav-items {
  display: none;
  flex-direction: column;
  align-items: center;
  padding-left: 0;
  margin: 0;
  list-style-type: none;
}

#nav-items.show {
  display: flex;
}

#nav-items li {
  padding: 10px 15px;
}

#nav-items a {
  color: var(--nav-li-color);
  text-decoration: none;
  font-weight: bold;
}

#nav-items a:hover {
  color: var(--nav-hover-color);
}

/* ============ Large Screens ============ */
@media (min-width: 768px) {
  #nav-toggle {
    display: none;
  }

  #nav-items {
    display: flex !important;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
  }
}

